@import "variables";
@import "mixins";



/*
    DEMO STYLE
*/
@import "https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700";
/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
body {
	font-family: 'Poppins', sans-serif;
	background: #fafafa;
}
p {
	font-family: 'Poppins', sans-serif;
	font-size: 1.1em;
	font-weight: 300;
	line-height: 1.7em;
}
a {
	color: inherit;
	text-decoration: none;
	transition: all 0.3s;
	&:hover {
		color: inherit;
		text-decoration: none;
		transition: all 0.3s;
	}
	&:focus {
		color: inherit;
		text-decoration: none;
		transition: all 0.3s;
	}
}
.navbar {
	padding: 15px 10px;
	background: #fff;
	border: none;
	border-radius: 0;
	box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}
.navbar-btn {
	box-shadow: none;
	outline: none !important;
	border: none;
}
.line {
	width: 100%;
	height: 1px;
	border-bottom: 1px dashed #ddd;
	margin: 40px 0;
}
.wrapper {
	display: flex;
	width: 100%;
	align-items: stretch;
}
#sidebar {
	min-width: 250px;
	max-width: 250px;
	background: $white-color;
	color: $secondary;
    transition: all 0.3s;
    background-color: #ffffff;
	.sidebar-header {
		background: #ebebeb;
        h3 {color:$white-color}
	}
	ul.components {
		padding: 20px 0;
        background:$white-color;
	}
	ul {
		p {
			color: $secondary;
            padding: 10px;
                &.profile_text {border-bottom:$border_bottom;font-size: 20px;}  
        }
        
		li { position: relative;
			a {
				padding: 13px 10px 13px 50px;
                font-weight: 500;
                font-size: 1.1em;
                color: $primary_black;
                display: block;

				&:hover {
					color: $primary_black;
                    background: $white-color;
				}
			}
		}
		li.active {
			>a {
				color: $primary_black;
                background:$white-color;
                &::before {
                    content:"";height: 10px; width: 40px; position: absolute;left: 0; top: 20px; background: $header_bg;
                }
			}
		}
	}
}
#sidebar.active {
	margin-left: -250px;
}
a[aria-expanded="true"] {
	color: #fff;
	background: #6d7fcc;
}
a[data-toggle="collapse"] {
	position: relative;
}

ul {
	ul {
		a {
			font-size: 0.9em !important;
			padding-left: 30px !important;
			background: #6d7fcc;
		}
	}
}
ul.CTAs {
	padding: 20px;
	a {
		text-align: center;
		font-size: 0.9em !important;
		display: block;
		border-radius: 5px;
		margin-bottom: 5px;
	}
}
a.download {
	background: #fff;
	color: #7386D5;
}
a.article {
	background: #6d7fcc !important;
	color: #fff !important;
	&:hover {
		background: #6d7fcc !important;
		color: #fff !important;
	}
}

// exchange Dashboard

section{
	.sec_sec {
	height: 70px;
	background: $header_bg;
	position: relative;
	}
}

section{
	.third_sec {
		position: absolute;
		top: -45px;
			.outer_radius{border-radius:$border-radius;box-shadow: $box-shadow;border-radius: $border-radius;}
		.bottom_border {
			border-bottom:1px solid#ccc;
		}
		h4	span {font-size: 18px;}
	}
}
.total_app {
	.outer_radius{border-radius:$border-radius;box-shadow: $box-shadow;border-radius: $border-radius;}
	margin-top: 135px;
		i{font-size: 22px;}
		.outer_radius{border-radius: 10px;}
}

.total_property {
	.outer_radius{border-radius:$border-radius;box-shadow: $box-shadow;border-radius: $border-radius;
		.header_text 
		h5 {background: $header_bg;background: #fec63d;padding: 12px 0;	@include top-radius;}
	}
}
.exchange {
	.outer_radius{
			@include top-radius;box-shadow: $box-shadow;border-radius: $border-radius;
			
		.header_text{
			background:$multi_bd ;
			@include top-radius;
				button{
					border-radius: 15px;
				}
		}
}
}

.third-exchange {
	position: absolute;
    top: -48px;
}

.token {
	margin-top: -25px;
	.outer_radius{
		border-radius:$border-radius;box-shadow: $box-shadow;border-radius: $border-radius;
		.recent_trading{
			button {border-radius:$border-radius-btn;    padding: 2px;}
		}
			.header_text{
				@include top-radius;
			}
	}	
}
.payment {
		.payment_tab {
			margin-top: 32px;
			margin-left: -16px;
			button{color:$primary_black;}
			.view {margin-left: -4px!important;
					
			}
		}
		.pymnt_btn 
			button { border-radius: $border-radius-btn; width: 105px;}
		
		}

.capital {
	.header_text {
		h6 {background:$multi_bd;@include top-radius;
			.custom_drop_btn { 
				button{color:$primary_black;	}
				.view {margin-left: -4px!important;color:$primary_black;
					
				}
			}
	
		}
	}
}

#content {
	width: 100%;
	min-height: 100vh;
    transition: all 0.3s;
        nav {background-color: #ebebeb;
            .header_icon {
                font-size: 20px;
            }
        }
}

// property-list

.property-list {
	.outer_radius {box-shadow: $box-shadow;border-radius: $border-radius;}
	.header-drop {
		select {
			height: 27px;
			padding-top: 0px;
			margin-top: 5px;
		}
	}
	.custom_table{
		.btn-1{    border-top-left-radius: 15px;border-bottom-left-radius: 15px;padding: 0 14px;}
		.btn-2{ border-top-right-radius: 15px;	border-bottom-right-radius: 15px; padding: 2px 10px;}
	}
	.bottom-btn {
		button {border-radius: $border-radius-btn;}
	}
}

	// New property-list

.new-property {
	.outer_radius {box-shadow: $box-shadow;}
	input[type=radio] {
		width: 25px;
		height: 25px;
		margin: 0 10px;
	}
	input[type=checkbox] {
		width: 20px;
		height: 20px;
		margin: 0 10px;
		border-radius: 0;
	}
	.amentites{
		label{
			font-size: 16px;
			padding: 10px 0;
		}
		.custom-file-control:lang(en)::after {
			content: "Choose file A...";
		}
	}
}

// property type
.property-type {
	.outer_radius {border-radius:$border-radius;box-shadow: $box-shadow;}
	.description { @include description-p }
	button {border-radius: $border-radius-btn;font-size: 16px;}
}

	//property-detail

.property-detail{
	.property-carousel {
		.carousel-control-next-icon, .carousel-control-prev-icon { width: 35px; height: 35px;}
		.carousel-indicators .active {background-color: $header_bg;height: 8px;width: 50px;}
		.carousel-indicators li {height: 8px;width: 50px;}	
	}
	.description {@include description-p }
	.outer_radius
		textarea {height: 123px;}
}

.map-responsive{
    overflow:hidden;
    padding-bottom:50%;
    position:relative;
	height:0;
	 iframe{  left:0;
		top:0;
		height:100%;
		width:100%!important;
		position:absolute;}
}

// agent-list

.agent-list {
	.star {
		span{font-size: 12px;}
	}
}
  
.banking {
	.show-btn{
		button{border-radius:$border-radius-btn}
	}
}


#sidebar.active+#content {}

#sidebar.active+#content nav button#sidebarCollapse {
    left: 0px;
}

nav#sidebar+#content nav button#sidebarCollapse {
    position: absolute;
    left: -60px;
}




@media (max-width: 1200px) {
	section .third_sec {
		position: static;top:unset;}
		.total_app {
			margin-top: 15px;
		}
}


@media (max-width: 768px) {
	#sidebar {
		margin-left: -250px;
	}
	#sidebar.active {
		margin-left: 0;
	}
	#sidebarCollapse {
		span {
			display: none;
		}
	}
}

